.nav {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  height: 60px;
  display: flex;
  justify-content: space-between;

  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav_black {
  background-color: #111;
}

.nav_logo {
  position: fixed;
  left: 50px;
  top: 20px;
  width: 80px;
  object-fit: contain;
}

.nav_avatar {
  position: fixed;
  right: 50px;
  width: 30px;
  object-fit: contain;
  border-radius: 5px;
  top: 20px;
}
